import axios from 'axios'
import { Message } from 'element-ui'
import store from '@/store'
import { getToken, setToken, setRefreshToken, getRefreshToken, removeToken, removeRefreshToken } from '@/utils/auth'
import { getRefreeshToken, } from '@/api/login'

// create an axios instance
const service = axios.create({
  baseURL: process.env.BASE_API, // api的base_url
  timeout: 5000 // request timeout
})

// request interceptor
service.interceptors.request.use(config => {
  // Do something before request is sent
  if (store.getters.token) {
    config.headers['Authorization'] = 'Bearer ' + getToken()
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone interceptor
service.interceptors.response.use(
  response => response,
  /**
   * 下面的注释为通过在response里，自定义code来标示请求状态
   * 当code返回如下情况则说明权限有问题，登出并返回到登录页
   * 如想通过xmlhttprequest来状态码标识 逻辑可写在下面error中
   * 以下代码均为样例，请结合自生需求加以修改，若不需要，则可删除
   */
  // response => {
  //   const res = response.data
  //   if (res.code !== 20000) {
  //     Message({
  //       message: res.message,
  //       type: 'error',
  //       duration: 5 * 1000
  //     })
  //     // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
  //     if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
  //       // 请自行在引入 MessageBox
  //       // import { Message, MessageBox } from 'element-ui'
  //       MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
  //         confirmButtonText: '重新登录',
  //         cancelButtonText: '取消',
  //         type: 'warning'
  //       }).then(() => {
  //         store.dispatch('FedLogOut').then(() => {
  //           location.reload() // 为了重新实例化vue-router对象 避免bug
  //         })
  //       })
  //     }
  //     return Promise.reject('error')
  //   } else {
  //     return response.data
  //   }
  // },
  error => {
    //token过期了刷新token
    if(error.response.data.message == 'Unauthenticated.'){
      let sendData = {
        'grant_type' : 'refresh_token',
        'refresh_token' : getRefreshToken(),
        'client_id' : 2,
        'client_secret' : '5IjevVN52Pe3GtiAy63WsViWfNj0L8igdscvcRtL',
        'scope' : '',
      }
      getRefreeshToken(sendData).then(res => {
        setToken(res.data.access_token)
        setRefreshToken(res.data.refresh_token)
        store.dispatch('GetUserInfo')
      }).catch(error => {
        if(error.response.status == 401){
          removeToken()
          removeRefreshToken()
          window.location.href = '/login'
        }else{
          Message({
            message: error.message,
            type: 'error',
            duration: 5 * 1000
          })
        }
      })
    }
    // console.log(JSON.stringify(error),'aaa')
    // console.log('err' + error)
    // Message({
    //   message: error.message,
    //   type: 'error',
    //   duration: 5 * 1000
    // })
    return Promise.reject(error)
  })

export default service



